import { UserManager, WebStorageStateStore } from 'oidc-client-ts'
import { oidcConfig } from './authConfig'
import Cookies from 'js-cookie'

class AuthService {
  constructor() {
    this.userManager = new UserManager({
      ...oidcConfig,
      userStore: new WebStorageStateStore({ store: window.localStorage })
    })

    this.userManager.events.addUserLoaded((user) => {
      Cookies.set('authToken', user.access_token, { expires: 7 })
      console.log('User loaded: ', user)
    })

    this.userManager.events.addUserUnloaded(() => {
      Cookies.remove('authToken')
      console.log('User logged out')
    })

    // X l gia hn token t ng
    this.userManager.events.addAccessTokenExpiring(() => {
      console.log('Access token sp ht hn. Bt u gia hn token...')
      this.userManager
        .signinSilent()
        .then((user) => {
          console.log('Access token mi:', user.access_token)
        })
        .catch((err) => {
          console.error('Li gia hn token:', err)
        })
    })

    this.userManager.events.addSilentRenewError((err) => {
      console.error('Gia hn token ngm tht bi:', err)
    })
  }

  login() {
    return this.userManager.signinRedirect()
  }

  logout() {
    return this.userManager.signoutRedirect()
  }

  getUser() {
    return this.userManager.getUser()
  }

  handleCallback() {
    return this.userManager.signinRedirectCallback()
  }

  callApi() {
    return this.getUser().then((user) => {
      if (user) {
        console.log("User's access token using for API:", user.access_token)

        // Thc hin gi API bo v
        const xhr = new XMLHttpRequest()
        xhr.open('GET', 'https://localhost:6002/api/movies') // URL ca API bo v
        xhr.setRequestHeader('Authorization', 'Bearer ' + user.access_token) // Gi access token trong header

        xhr.onload = function () {
          console.log('API response: ', xhr.responseText)
        }

        xhr.onerror = function () {
          console.error('Error while calling API')
        }

        xhr.send()
      } else {
        console.error('User is not logged in')
      }
    })
  }
}

const authServiceInstance = new AuthService()
export default authServiceInstance
